  transform: translateY(-100%);\n    opacity: 0;\n  }\n  100% {\n    transform: translateY(0);\n    opacity: 1;\n  }\n}\n\n.popUpScale {\n  animation: popUpScale 0.5s ease-out;\n}\n\nnav {\n  font-weight: bold;\n  background-color: $primary-color;\n  padding: $padding-large;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  animation: popUpDrop 0.5s ease-out;\n  position: sticky;\n  top: 0;\n  z-index: 99;\n\n  @media (max-width:360px){\n    flex-direction: column;\n  }\n\n  .brand {\n    display: flex;\n    align-items: center;\n    gap: $base-spacing;\n    img {\n      width: 3rem;\n      height: 3rem;\n    }\n  }\n  ul {\n    display: flex;\n    list-style: none;\n    gap: $double-spacing;\n    @media (max-width: 768px) {\n      position: fixed;\n      top: 5rem;\n      right: -100%;\n      padding: $padding-large;\n      flex-direction: column;\n      justify-content: center;\n      background-color: $primary-color;\n      transition: right 0.3s ease;\n      z-index: 5;\n\n      &.open {\n        right: 0;\n      }  \n    }\n\n    li {\n      margin: 0 $half-spacing;\n      a {\n        color: $text-color;\n        text-decoration: none;\n        transition: all 0.3s ease-in-out;\n        padding: $padding-large;\n        &:hover {\n          background-color: $background-color;\n          border-radius: $border-radius;\n        }\n      }\n    }\n  }\n  #hamburger-menu {\n    display: none;\n    flex-direction: column;\n    justify-content: space-around;\n    width: 2rem;\n    height: 2rem;\n    background: transparent;\n    border: none;\n    cursor: pointer;\n    min-height: 3rem;\n    min-width: 3rem;\n    border-radius: $border-radius;\n    z-index: 10;\n    animation: popUpScale 0.5s ease-out;\n    &:hover {\n      background-color: darken($primary-color, 10%);\n    }\n    &:focus {\n      background-color: darken($primary-color, 10%);\n    }\n    @media (max-width: 768px) {\n      display: flex;\n    }\n    div {\n      margin: auto;\n      width: 2rem;\n      height: 0.25rem;\n      background: #333;\n      border-radius: 10px;\n      transition: all 0.3s linear;\n      position: relative;\n      transform-origin: 1px;\n    }\n  } \n}\n\nmain {\n  background-color: #FFF;\n  display: flex;\n  flex-direction: column;\n  gap: $double-spacing;\n}\n\n.skip-link {\n  position: absolute;\n  top: -2rem;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  background-color: $button-color;\n  color: white;\n  padding: 1rem;\n  z-index: 100;\n  transition: all 0.3s ease-in-out;\n  &:hover {\n    background-color: darken($button-color, 10%);\n  }\n  &:focus {\n     top: 2rem;\n     left: 50%;\n     transform: translate(-50%, -50%);\n  }\n}\n\nsection {\n  padding: $padding-large*2;\n  @media (min-width: 1200px) {\n    padding: $padding-large*4;\n  }\n  .title {\n    text-align: center;\n    margin-bottom: $base-spacing;\n  }\n}\n\n.title.hero {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  text-align: center;\n  animation: popUpScale 0.5s ease-out;\n  \n  img {\n    width: 100%;\n    @media (min-width: 1200px) {\n      margin-top: $double-spacing;\n      min-width: 1000px;\n      width: 0;\n      border: 0.125rem solid $border-color;\n      border-radius: $border-radius;\n    }\n  }\n\n  h1 {\n    margin-bottom: $base-spacing;\n    bottom: 50%;\n    font-size: 2.5em;\n  }\n\n  .motto {\n    top: 50%;\n    font-size: 1.2em;\n  }\n\n  h1, .motto {\n    position: absolute;\n    width: 100%;\n    color: #fff;\n    background-color: rgba(0, 0, 0, 0.5);\n    padding: 10px 0;\n    @media (min-width: 1200px) {\n      min-width: 1000px;\n      width: 0;\n    }\n  }\n}\n\n.restaurant-image-container {\n  display: flex;\n  overflow-x: auto;\n  width: 100%;\n  gap: $base-spacing;\n  padding-bottom: $base-spacing;\n  &::-webkit-scrollbar {\n    display: none;\n  }\n  img {\n    flex: 0 0 auto;\n    border: 0.125rem solid $border-color;\n    border-radius: $border-radius;\n    box-shadow: $box-shadow $border-color;\n  }\n}\n\n#restaurant-item-container {\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: $double-spacing;\n  @media (min-width: 600px) {\n    grid-template-columns: repeat(2, 1fr);\n  }\n\n  @media (min-width: 992px) {\n    grid-template-columns: repeat(3, 1fr);\n  }\n  \n  restaurant-item {\n    display: flex;\n    flex-direction: column;\n    border: 0.125rem solid $border-color;\n    border-radius: $border-radius;\n    box-shadow: $box-shadow $border-color;\n    position: relative;\n    img {\n      width: 100%;\n      height: 20rem;\n      border-radius: $border-radius;\n      border-bottom-left-radius: 0;\n      border-bottom-right-radius: 0;\n    }\n    > p {\n      font-weight: bold;\n      padding: $padding-small;\n      text-align: center;\n      background-color: $primary-color;\n      position: absolute;\n      top: 1rem;\n      left: 0;\n      border-top-right-radius: $border-radius;\n      border-bottom-right-radius: $border-radius;\n      box-shadow: $box-shadow $border-color;\n    }\n    .restaurant-item-body {\n      background-color: $background-color;\n      padding: $padding-large*2;\n      .rating {\n        margin-bottom: $base-spacing;\n      }\n      p {\n        text-align: justify;\n        overflow-y: auto;\n        max-height: 8rem;\n        &::-webkit-scrollbar {\n          display: none;\n        }\n      }\n    }\n  }\n}\n\nfooter {\n  background-color: $secondary-color;\n  padding: $padding-large;\n  text-align: center;\n  font-weight: bold;\n}"],"sourceRoot":""}]);
